<template>
    <div>
        <head-top webActive="home"></head-top>
        <div class="bgLogin" id="login">
            <div class="container">
                <div class="loginBg">
                    <div class="regBox">
                        <div class="login-panel">
                            <h2 class="tl">重设密码</h2>
                            <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="forgetForm mt15">
                                <el-form-item label="手机号" prop="mobile">
                                    <el-input v-model.number="form.mobile" type="number" placeholder="请输入手机号"></el-input>
                                </el-form-item>
                                <el-form-item label="校验码" prop="piccode">
                                    <el-row>
                                        <el-col :span="12">
                                            <el-input v-model="form.piccode" placeholder="请输入图形校验码"></el-input>
                                        </el-col>
                                        <el-col :offset="1" :span="11" style="height: 40px;">
                                            <img class="vertifyPic" :src="vertifyUrl" @click="freshVertify()">
                                        </el-col>
                                    </el-row>
                                </el-form-item>
                                <el-form-item label="验证码" prop="code">
                                    <el-row>
                                        <el-col :span="12">
                                            <el-input v-model="form.code" placeholder="请输入短信验证码"></el-input>
                                        </el-col>
                                        <el-col :offset="1" :span="11" >
                                            <el-button type="primary" class="w" :disabled="computedTime?true:false" @click.prevent="getVerifyCode">{{computedTime?'已发送('+computedTime+'s)':'获取短信验证码'}}</el-button>
                                        </el-col>
                                    </el-row>
                                </el-form-item>
                                <el-form-item label="密码" prop="password">
                                    <el-input v-model="form.password" placeholder="请输入密码" show-password></el-input>
                                </el-form-item>
                                <el-form-item label="再输密码" prop="password2">
                                    <el-input v-model="form.password2" placeholder="请再次确认密码" show-password></el-input>
                                </el-form-item>
                                <el-button type="primary" @click="submitForm()" class="w mt10 mb15">确定</el-button>
                            </el-form>


<!--                            <form>-->
<!--                                <div class="loginPhone pr">-->
<!--                                    <img src="../../images/iconPhone.png">-->
<!--                                    <input class="w" type="number" name="phone" placeholder="请输入手机号">-->
<!--                                </div>-->
<!--                                <div class="flex f-nowrap j-bet loginInput mt15 pr">-->
<!--                                    <img src="../../images/iconMessage.png">-->
<!--                                    <div class="loginCode w">-->
<!--                                        <input class="w" type="number" name="code" placeholder="短信验证码">-->
<!--                                    </div>-->
<!--                                    <a href="" class="loginCodeBtn">获取验证码</a>-->
<!--                                </div>-->
<!--                                <div class="loginPhone pr">-->
<!--                                    <img src="../../images/iconPassword.png">-->
<!--                                    <input class="w" type="password" name="phone" placeholder="6-20位字母或数字组合">-->
<!--                                </div>-->
<!--                                <div class="loginPhone pr">-->
<!--                                    <img src="../../images/iconPassword.png">-->
<!--                                    <input class="w" type="password" name="phone" placeholder="请再次确认密码">-->
<!--                                </div>-->
<!--                                <a href="" class="loginBtn mb10">确定</a>-->
<!--                            </form>-->
                        </div>
                        <div class="loginP_bottom tc">已有账号？<router-link class="colorGreen" :to="{path:'/login/login',query:{}}">登录</router-link></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import headTop from '../../components/header/headTop';
    import {getCode,forGetKey} from '../../service/api'
    export default {
        name: 'register',
        data(){
            return{
                vertifyUrl:'',
                form:{
                    mobile:'',
                    piccode:'',//图形校验码
                    code:'',
                    codeToken:'',
                    password:'',
                    password2:''
                },
                computedTime: 0, //倒数记时
                rules:{
                    mobile: [
                        { required: true, message: '请输入手机号',trigger: 'blur'},
                        { type: 'number', message: '手机号必须为数字值',trigger: 'blur'},
                        {
                            pattern: /^1\d{10}$/,
                            message: '手机号格式不对',
                            trigger: 'blur'
                        }
                    ],
                    piccode:[
                        { required: true, message: '请输入图形校验码',trigger: 'blur'},
                        {
                            pattern: /^[0-9]*$/,
                            message: '请输入数字',
                            trigger: 'blur'
                        }
                    ],
                    code:[
                        { required: true, message: '请输入短信验证码',trigger: 'blur'},
                        {
                            pattern: /^[0-9]*$/,
                            message: '请输入数字',
                            trigger: 'blur'
                        }
                    ],
                    password:[
                        {required: true, type: 'string', message: '请输入密码', trigger: 'blur'}
                    ],
                    password2:[
                        {required: true, type: 'string',message: '请再次输入密码' , trigger: 'blur'}
                    ]
                },

            }
        },
        components:{
            headTop
        },
        mounted(){
            this.initData();

        },
        computed:{


        },
        methods:{
            async initData(){
                this.freshVertify();
            },
            submitForm() {
                this.$refs['form'].validate(async (valid) => {
                    if (valid) {
                        if(this.form.password == this.form.password2){
                            var res = await forGetKey(
                                this.form.password,
                                this.form.password2,
                                this.form.code,
                                this.form.codeToken
                            );

                            if(res.status==12000){
                                this.$message({
                                    message: res.msg,
                                    type: 'success'
                                });
                                this.$router.push({ name: '/login/login', params: {}});
                            }else{
                                this.$message({
                                    message: res.msg,
                                    type:'warning'
                                });
                            }
                        }else{
                            this.$message({
                                message:'两次输入密码不一致',
                                type:'warning'
                            })
                        }


                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //获取短信验证码
            async getVerifyCode(){
                if (this.form.mobile=='') {
                    this.$message({
                        message: '手机号码不能为空',
                        type: 'warning'
                    });
                }else if(!(/^1\d{10}$/gi.test(this.form.mobile))){
                    this.$message({
                        message: '手机格式不正确',
                        type: 'warning'
                    });
                }else if(this.form.piccode==''){
                    this.$message({
                        message: '图形校验码不能为空',
                        type: 'warning'
                    });
                }else{
                    this.computedTime = 30;
                    this.timer = setInterval(() => {
                        this.computedTime --;
                        if (this.computedTime == 0) {
                            clearInterval(this.timer)
                        }
                    }, 1000);

                    var res = await getCode(
                        this.form.mobile,
                        this.form.piccode,
                        'forGetKey'
                    );
                    if(res.status==12000){
                        this.form.codeToken = res.codeToken || '';
                        this.$message({
                            message: res.msg,
                            type: 'success'
                        });
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                        this.freshVertify();

                    }
                }
            },
            freshVertify(){
                this.vertifyUrl = 'http://api.sixmol.com/api/v1/VerifyHelper?i=' + Math.floor((Math.random() * 10) + 1);
            },

        },
        created(){

        },

    }

</script>

<style scoped>
    /*登录*/
    .bgLogin{
        background: -webkit-linear-gradient(30deg,#007991, #78ffd6); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(30deg,#007991, #78ffd6); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(30deg,#007991, #78ffd6); /* Firefox 3.6 - 15 */
        background: linear-gradient(30deg,#007991, #78ffd6); /* 标准的语法 */
    }
    .loginBg{padding:80px 0;}
    .login-des{width: 100%;text-align: center;margin-right: 30px;flex-shrink: 1;}
    .login-des img{width:500px;margin:0 auto;}
    .loginBox{min-width: 420px;}
    .login-panel{background-color: #fff;border-radius: 10px 10px 0 0;padding:15px 30px;flex-shrink: 0;}
    .loginT{font-size: 16px;text-align: center;width:50%;line-height: 40px;cursor: pointer;}
    .loginT.active{border-bottom: 3px solid #0bb8b2;}
    .loginPhone{border:1px solid #ddd;height: 40px;line-height: 40px;margin-top:15px;padding:0 15px 0 45px;}
    .loginPhone input{height: 40px;line-height: 40px;}
    .loginPhone img{width: 24px;height: 24px;position: absolute;top:8px;left: 10px;}
    .loginInput img{width: 24px;height: 24px;position: absolute;top:8px;left: 10px;}
    .loginCode{border:1px solid #ddd;height: 40px;line-height: 40px;padding:0 15px 0 45px;flex-shrink: 1;margin-right: 15px;}
    .loginCode input{height: 40px;line-height: 40px;}
    .loginCodeBtn{background-color: #0bb8b2;color:#fff;height: 40px;line-height: 40px;padding:0 30px;flex-shrink: 0;}
    .loginBtn{background-color: #0bb8b2;color:#fff;height: 40px;line-height: 40px;width:100%;text-align: center;display: block;margin-top: 15px;font-size: 16px;}
    .loginCodeBtn:hover,.loginBtn:hover{background-color:#0a9e9b;color:#fff;}
    .loginP_bottom{background-color: #ddd;color:#333;border-top:1px solid #eee;border-radius: 0 0 10px 10px;line-height: 60px;}
    .loginP_bottom a:hover{text-decoration: underline;}
    .vertifyPic{
        height:40px;
        width:100%;
    }

    /*注册*/
    .regBox{width: 500px;margin:0 auto;}
    .regBox h2,.regBox h3{font-size:28px;font-weight: bold;line-height: 36px;color:#0bb8b2;}



</style>



